import React from 'react';
import './App.css';
import Carousel from 'react-elastic-carousel'
import {FaBolt, FaArrowLeft, FaLandmark, FaSearchLocation, FaMapMarker, FaMapMarkerAlt, FaChalkboardTeacher, FaPencilRuler, FaInfoCircle } from 'react-icons/fa';
import ReactAvatar from 'react-avatar';
import Box from '@material-ui/core/Box';
import {
  HashRouter as Router,
  Route,
} from 'react-router-dom'
import ReadMoreReact from 'read-more-react';
import {db} from './firebase'
import ClassPage from './ClassPage';

var name
var colorOverlay

function Child ({match}) {
  name = match.params.id
  return(
    <div>
    </div>
  )
}

class ClassesDisplay extends React.Component{
  state = {
    classes: null,
    images: null,
    eligibility: null,
    qualifications: null,
    note: null,
    courses: null,
    offers: null
  }

  componentDidMount(){
    const data = db.collection('Classes').doc(name).collection('Information');    
     data.get()
      .then(snapshot=>{
        const classes = []
        snapshot.forEach(doc=>{
          const data = doc.data()
          classes.push(data)
        })
        this.setState({classes:classes})
      })

    const images = db.collection('Classes').doc(name).collection('Images');    
      images.get()
      .then(snapshot=>{
        const images = []
        snapshot.forEach(doc=>{
          const data = doc.data()
          images.push(data)
        })
        this.setState({images:images})
      })

      const eligibility = db.collection('Classes').doc(name).collection('Eligibility');    
      eligibility.get()
      .then(snapshot=>{
        const eligibility = []
        snapshot.forEach(doc=>{
          const data = doc.data()
          eligibility.push(data)
        })
        this.setState({eligibility:eligibility})
      })

      const qualifications = db.collection('Classes').doc(name).collection('Qualifications');    
      qualifications.get()
      .then(snapshot=>{
        const qualifications = []
        snapshot.forEach(doc=>{
          const data = doc.data()
          qualifications.push(data)
        })
        this.setState({qualifications:qualifications})
      })

      const note = db.collection('Classes').doc(name).collection('Note');    
      note.get()
      .then(snapshot=>{
        const note = []
        snapshot.forEach(doc=>{
          const data = doc.data()
          note.push(data)
        })
        this.setState({note:note})
      })

      const courses = db.collection('Classes').doc(name).collection('Courses');    
      courses.get()
      .then(snapshot=>{
        const courses = []
        snapshot.forEach(doc=>{
          const data = doc.data()
          courses.push(data)
        })
        this.setState({courses:courses})
      })

      const offers = db.collection('Classes').doc(name).collection('Offers');    
      offers.get()
      .then(snapshot=>{
        const offers = []
        snapshot.forEach(doc=>{
          const data = doc.data()
          offers.push(data)
        })
        this.setState({offers:offers})
      })
  }

  render() {
    return (
      <Router class='root'>
        <div class='overlayContainer'>
        {
          this.state.classes&&
          this.state.classes.map(classes=>{
            return(
              <div class='carouselContainer'> 
                {
                  this.state.images&&
                  this.state.images.map(image=>{
                  return(
                          <img src={image.item} height='250px' width='auto' class='imageCarousel'/>        
                        )
                  })
                }       
              </div>    
            )
          })
        }
        <div class='overlayBlack'><FaArrowLeft color='#FFFF' class='backIcon'/></div>
        </div>
        <div>
        {
          this.state.classes&&
          this.state.classes.map(classes=>{
             return(
                   <div class='displayTitle'>
                      Name
                      <div class='mapIcon'>
                        <FaMapMarkerAlt color='#043540'/>
                        <div style={{fontSize:'10px'}} >Map</div>
                      </div>
                   </div>      
                 )
              })
        }               
        </div>
        <div class='displaySubtitle'>
          Adress
          <br/>
          <br/>
        </div>
        <div class='carouselContainer'>
          {
            this.state.offers&&
            this.state.offers.map(offers=>{
              return(
                <div class='offers'>  
                          
                </div>
                )
            })
          }
        </div>


        <hr color='#E6E6E6' size='4' style={{margin:'0px'}}/>

        <div style={{backgroundColor:'#04BF7B',paddingTop:'5px'}}>
        <div class='displaySubtitle2'>
          <FaInfoCircle color='#043540' size='20' style={{marginRight:'10px'}}/>Eligibility Criteria
        </div>
        <div class='displaySubtitle'>
          <div class='carouselContainer'style={{backgroundColor:'#04BF7B'}}>
        {
          this.state.eligibility&&
          this.state.eligibility.map(eligibility=>{
            return(
              <div class='list'>
                {eligibility.item}
              </div>
              )
          })
        }
        </div>
        </div>
        </div>

        <div class='carouselContainer' >
        {
          this.state.courses&&
          this.state.courses.map(course=>{
            return(
              <div class='classCard'>
                <ReactAvatar src={course.image} round='50%' size='60'/><br/>
                {course.title}
                <br/>
                {course.price}
                <br/>
                {course.details}
              </div>
              )
          })
        }
        </div>
        
        <div class='displaySubtitle2'>
          <FaChalkboardTeacher color='#043540' size='20' style={{marginRight:'10px'}}/>  About Faculty
        </div>
        <div class='displaySubtitle'>
        {
          this.state.qualifications&&
          this.state.qualifications.map(qualifications=>{
            return(
              <ul>
                <li>
                {qualifications.item}
                </li>
              </ul>
              )
          })
        }
        </div>

        <hr color='#E6E6E6' />

        <div class='displaySubtitle2'>
          <FaPencilRuler color='#043540' size='20' style={{marginRight:'10px'}}/> Note From Teacher
        </div>
        <div class='displaySubtitle'>
        {
          this.state.note&&
          this.state.note.map(note=>{
            return(
              <div style={{marginLeft:'10px'}} >
                <ReadMoreReact text={"Note"}
                min={10}
                ideal={40}
                max={70}
                readMoreText="read more"/>
              </div>
              )
          })
        }
        </div>
        <Route exact path="/:id" component={Child}/>
        <Route exact path="/classes" component={ClassPage}/>
      </Router>
    )
  }
}
export default ClassesDisplay;
